IEditUser = compose(
    reduxForm({ form: 'editUserById' ,
    keepDirtyOnReinitialize: true,
    enableReinitialize: true,
    updateUnregisteredFields: true })
)(IEditUser);

export default connect(state => ({ 
    // alternatively, you can set initial values here...
    initialValues: {
      nombre_usuario: state.user.json_data.message.nombre_usuario,
      apellido_usuario: state.user.json_data.message.apellido_usuario,
      correo_usuario: state.user.json_data.message.correo_usuario,
      cod_usuario:  state.user.json_data.message.cod_usuario,
      cod_rol:  state.user.json_data.message.rol.cod_rol,
      cod_estado:  state.user.json_data.message.cod_estado


    } 
  }))(IEditUser);