import React from 'react';
import IListUsers from './page';

//REACT-REDUX dependencies
import { connect } from 'react-redux';
import { getListUsers, removeUserById, removeUserSuccess } from '../../../../actions/general/user'

//ANT DEPENDENCIES
import { Modal, Button, Progress } from 'antd';

class  ListUsers extends React.Component{
  
  state = {setVisible: false, setVisibleOnSecondModal : false ,  userRemoveId: 0}

  //propiedades de manipulación de la ventana modal
  deleteUser = (e) => {
    this.setState({
      setVisible : true,
      userRemoveId : e
    })
  }

  closeModal = (e) => {
    this.setState({
      setVisible : false,
      setVisibleOnSecondModal: true
    })
  }

  okModal = () => {
    this.props.removeUserById(this.state.userRemoveId);
    this.closeModal(0);
  }


   componentDidMount(){
    this.props.getListUsers(10,1);
  }

   onChangePage = (p) => {
     console.log(p);
   }

  render(){ 
    const { listUsers,removeUser } = this.props;
    
    return(
      <React.Fragment>
        <IListUsers listUsers={listUsers} deleteUser={this.deleteUser.bind(this)} onChangePage={this.onChangePage.bind(this)}/>

        <Modal 
        visible={this.state.setVisible}
        onOk={this.okModal}
        onCancel={this.closeModal}
        >
             <center><Progress type="circle" percent={100} status="exception" /><h2>Desactivar usuario</h2>¿Está seguro de deshabilitar este usuario?</center> 
        </Modal>

        <Modal 
        visible={this.state.setVisibleOnSecondModal}
        onOk={() => this.setState({setVisibleOnSecondModal : false}) }
        onCancel={() => this.setState({setVisibleOnSecondModal : false}) }
        >
      {removeUser.success && <center><Progress type="circle" percent={100} /><h2>¡Perfecto!</h2>La operación se ha realizado correctamente.</center> || 
            <center><Progress type="circle" percent={100} status="exception" /><h2>Ha ocurrido algo..</h2>La operación no se pudo realizar.</center> }        </Modal>
      </React.Fragment>
    );

  }

}

const mapStateToProps = (state) => {
  return{
    listUsers : state.user,
    removeUser : state.removeUser
  }
}

const mapDispatchToProps = {
  getListUsers,
  removeUserById
}

export default connect(mapStateToProps, mapDispatchToProps)(ListUsers);