import { createAction } from 'redux-actions';
import { e_port , e_route } from '../../thirdParty/params'
import axios from 'axios';

export const handleApiError = createAction('handleApiError');

export  const getMateriaPrimaPruduccionSuccess = createAction('getMateriaPrimaPruduccionSuccess');
export  const getMateriaPrimaAlmacenadaSuccess = createAction('getMateriaPrimaAlmacenadaSuccess');
export  const crearMateriaPrimaSuccess = createAction('crearMateriaPrimaSuccess');


export const getProduccion = () => {
    return async dispatch => {
        return axios.get(`${e_route}:${e_port}/logistica/materiaprima/produccion`)
        .then(( data ) => {
            dispatch(getMateriaPrimaPruduccionSuccess(data));
       }).catch((error) => {
           alert(error)
            dispatch(handleApiError(error));
       });
      }
}

export const getAlmacenada = () => {
    return async dispatch => {
        return axios.get(`${e_route}:${e_port}/logistica/materiaprima/almacenada`)
        .then(( data ) => {
            dispatch(getMateriaPrimaAlmacenadaSuccess(data));
       }).catch((error) => {
           alert(error)
            dispatch(handleApiError(error));
       });
      }
}


export const createRawMaterial = (nom_logistica, valor, cantidad, cod_tipo_logistica, cod_estado_logistica) => {
    return async dispatch => {
        return axios.put(`${e_route}:${e_port}/logistica/`, {
            nom_logistica, valor, cantidad, cod_tipo_logistica, cod_estado_logistica
        })
        .then(( data ) => {
            dispatch(crearMateriaPrimaSuccess(data));
       }).catch((error) => {
           alert(error)
            dispatch(handleApiError(error));
       });
      }
}




