import React from 'react';
import IEgress from './page';

//REACT-REDUX dependencies
import { connect } from 'react-redux';
import { getEgreso } from '../../../../actions/general/finanza'

//ANT DEPENDENCIES
import { Modal, Button, Progress } from 'antd';

class Egress extends React.Component{
  
  state = {setVisible: false, setVisibleOnSecondModal : false ,  userRemoveId: 0}


   componentDidMount(){
    this.props.getEgreso();
  }

   onChangePage = (p) => {
     console.log(p);
   }

  render(){ 
    const { listEgreso } = this.props;
    
    return(
      <React.Fragment>
        <IEgress listEgreso={listEgreso}   onChangePage={this.onChangePage.bind(this)}/>
      </React.Fragment>
    );

  }

}

const mapStateToProps = (state) => {
  return{
    listEgreso : state.getFinanzaEgreso
  }
}

const mapDispatchToProps = {
    getEgreso
}

export default connect(mapStateToProps, mapDispatchToProps)(Egress);