import React from 'react';
import IFrame from 'react-iframe'; 
class ExamplePath extends React.Component{
  state = {url:''}
  constructor(props) {
    super(props);

  }

  componentDidMount(){
    let search = window.location.search;
    let params = new URLSearchParams(search);
    let foo = params.get('p');
    let z = params.get('z');
    console.log('hla' + z)
    if(z == null){
      foo = "https://dmcorrales.me/ERP" + foo + "?id=" + JSON.parse(sessionStorage.getItem('myTokenData')).cod_usuario;
    }else{
      foo = "http://vapedraza.site/erplogistica" + foo;
    }
    this.setState({
      url:foo
    })
  }

  render(){ 

    return(
      
      <IFrame 
      url={this.state.url}
      width="100%"
      height="100%"
      display="initial"
      frameBorder="0"
      position="relative"
      />
    );

  }

}

export default ExamplePath;