import React from 'react';
import AuthLayout from './layout/AuthLayout'
import LoginPage from './container/login';

//reducers
import { connect } from 'react-redux';
import {login} from '../../actions/authentication';
import { Redirect } from 'react-router-dom'

import { Router } from 'react-router';

class LoginComponent extends React.Component
{

    constructor(){
        super();
        this.onSubmit = this.onSubmit.bind(this);
    }

    isReady(){
        if (this.props.auth.authToken === void 0) {
            return true;
           }else if(this.props.auth.authToken !== void 0){
               if(!this.props.auth.authToken.success && this.props.auth.authToken.message === "Credenciales incorrectas"){
                return false;
               }else if(this.props.auth.authToken.success && this.props.auth.authToken.message !== "Credenciales incorrectas"){
                sessionStorage.setItem('myTokenData', JSON.stringify(this.props.auth.authToken.message))
                this.redirectToWelcome();
                return true;
                }else{
                return true;
            }
      }
    }

    redirectToWelcome(){
        window.location.href=("/admin/welcome")
        }
  

    async onSubmit(formData){
        console.log("data",formData)
        await this.props.login(formData)
    }

    render(){
        return(
            <React.Fragment>
                <AuthLayout>
                    {<p>
                        {this.isReady() ?<p></p> : 
                            <div class="alert alert-danger" role="alert">
                            {this.props.auth.authToken.message}
                          </div>
                            }
                      </p>}
                      <LoginPage onSubmit={this.onSubmit} />
     
                </AuthLayout>
            </React.Fragment>
        );
    }

}


const mapStateToProps = (state) => {
    return {
       auth: state.auth
    }
 }
 
 const mapDispatchToProps = {
    login
 }

export default connect(mapStateToProps, mapDispatchToProps)(LoginComponent);