import { createAction } from 'redux-actions';
import { e_port , e_route } from '../../thirdParty/params'
import axios from 'axios';

export const handleApiError = createAction('handleApiError');

export  const getProductoEnviadoSuccess = createAction('getProductoEnviadoSuccess');
export  const getProductoEntregadoSuccess = createAction('getProductoEntregadoSuccess');
export  const crearMateriaPrimaSuccess = createAction('crearMateriaPrimaSuccess');


export const getEnviado = () => {
    return async dispatch => {
        return axios.get(`${e_route}:${e_port}/logistica/producto/enviado`)
        .then(( data ) => {
            dispatch(getProductoEnviadoSuccess(data));
       }).catch((error) => {
           alert(error)
            dispatch(handleApiError(error));
       });
      }
}

export const getEntregado = () => {
    return async dispatch => {
        return axios.get(`${e_route}:${e_port}/logistica/producto/entregado`)
        .then(( data ) => {
            dispatch(getProductoEntregadoSuccess(data));
       }).catch((error) => {
           alert(error)
            dispatch(handleApiError(error));
       });
      }
}


export const createRawMaterial = (nom_logistica, valor, cantidad, cod_tipo_logistica, cod_estado_logistica) => {
    return async dispatch => {
        return axios.put(`${e_route}:${e_port}/logistica/`, {
            nom_logistica, valor, cantidad, cod_tipo_logistica, cod_estado_logistica
        })
        .then(( data ) => {
            dispatch(crearMateriaPrimaSuccess(data));
       }).catch((error) => {
           alert(error)
            dispatch(handleApiError(error));
       });
      }
}




