import { getListCategoriesSuccess, handleApiError } from '../../actions/general/categories';
import { handleActions } from 'redux-actions'
import { errors } from '../../dependencies/lang/es'

const defaultState = {
    json_data: [],
    message: '',
    success: '',
    loading: true
};

export default handleActions({ 
    [getListCategoriesSuccess]: (state, {type, payload}) => {
        return { ...state, json_data: payload.data.message, message: undefined, success: true, loading: false }
    },
    [handleApiError]: (state, {type,payload} ) => {
        return { ...state, json_data: [] , message: errors.no_data_found, success: true, loading: false } 
    }
}, defaultState)


