import { getNominaByUserSuccess ,handleApiError } from '../../actions/general/nomina';
import { handleActions } from 'redux-actions';

const defaultState = {
    json_data: [],
    message: '',
    success: '',
    loading: true  
};

export default handleActions({
    [getNominaByUserSuccess]:(state, {type, payload}) => { 
        return { ...state, json_data: payload.data, message: undefined, success:true, loading:false}
    },
    [handleApiError]:(state, {type, payload}) => {
        return { ...state, json_data: [], message: undefined , success:true, loading:false}
    }
}, defaultState)
