import { createAction } from 'redux-actions';
import { e_port , e_route } from '../../thirdParty/params'
import axios from 'axios';

export const handleApiError = createAction('handleAppiErrorNomina');
export const getNominaSuccess = createAction('getNominaSuccess');
export const getNominaByUserSuccess = createAction('getNominaByUserSuccess');

export const getListNomina = (range, page) => {
    return async dispatch => {
        return axios.get(`${e_route}:${e_port}/usuario/nomina`)
        .then(( data ) => {
            dispatch(getNominaSuccess(data));
       }).catch((error) => {
            dispatch(handleApiError(error));
       });
      }
}

export const getNominaByUser = (id) => {
    return async dispatch => {
        return axios.get(`${e_route}:${e_port}/usuario/nomina/${id}`)
        .then(( data ) => {
            dispatch(getNominaByUserSuccess(data));
       }).catch((error) => {
            dispatch(handleApiError(error));
       });
      }
}
