import React from 'react';
import {connect} from 'react-redux';
import { createRawMaterial } from '../../../../actions/general/materiaprima';
import ICreateRawMaterial from './page'
//ANT DEPENDENCIES
import { Modal, Button, Progress } from 'antd';

class CreateRawMaterial extends React.Component{

    state = { visible: false, errorsValidation:false }

  
    //nom_logistica, valor, cantidad, cod_tipo_logistica, cod_estado_logistica
    async onSubmit(formData){
        //se validan los datos
        //se actualizan los datos recibidos
        this.props.createRawMaterial( formData.nom_logistica, formData.valor, formData.cantidad, formData.cod_tipo_logistica, formData.cod_estado_logistica);
        this.setState({
          visible: true,
      });
      }
  
      handleOk = (e) => {
        console.log(e);
        this.setState({
            visible: false,
        });
      }
  
      handleError = (e) => {
        this.setState({
          visible: true,
          errorsValidation: true
        });
      }
      
      handleCancel = (e) => {
        console.log(e);
        this.setState({
          visible: false,
        });
      }

      isReady(){
        if (this.props.rawmaterial.success === void 0 ) {
            return true;
           }else{
             return false;
        }   
      }
  

    render(){
        const {rawmaterial } = this.props;
        return(
            <React.Fragment>
                <ICreateRawMaterial rawmaterial={rawmaterial} onSubmit={this.onSubmit.bind(this)}/>

            <Modal
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            >

            <center><Progress type="circle" percent={100} /><h2>¡Perfecto!</h2>La operación se ha realizado correctamente.</center> 
            
            </Modal>
            </React.Fragment>
        );
    }
}

const mapStateToProps = (state) =>{
    return {
        rawmaterial : state.createRawaterial
    }
}

const mapDispatchToProps = {
    createRawMaterial
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateRawMaterial);