import React from 'react';
import {connect} from 'react-redux';
import { createFinance } from '../../../../actions/general/finanza';
import ICreateFinance from './page'
//ANT DEPENDENCIES
import { Modal, Button, Progress } from 'antd';

class CreateFinance extends React.Component{

    state = { visible: false, errorsValidation:false }

  
    //nom_logistica, valor, cantidad, cod_tipo_logistica, cod_estado_logistica
    async onSubmit(formData){
        //se validan los datos
        //se actualizan los datos recibidos

        this.props.createFinance( formData.cod_tipo_finanza, formData.cod_tipo_transaccion, formData.val_transaccion);
        this.setState({
          visible: true,   
      });
      }
  
      handleOk = (e) => {
        console.log(e);
        this.setState({
            visible: false,
        });
      }
  
      handleError = (e) => {
        this.setState({
          visible: true,
          errorsValidation: true
        });
      }
      
      handleCancel = (e) => {
        console.log(e);
        this.setState({
          visible: false,
        });
      }

      isReady(){
        if (this.props.finanza.success === void 0 ) {
            return true;
           }else{
             return false;
        }   
      }
  

    render(){
        const {finanza } = this.props;
        return(
            <React.Fragment>
                <ICreateFinance finanza={finanza} onSubmit={this.onSubmit.bind(this)}/>

            <Modal
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            >

            <center><Progress type="circle" percent={100} /><h2>¡Perfecto!</h2>La operación se ha realizado correctamente.</center> 
            
            </Modal>
            </React.Fragment>
        );
    } 
}

const mapStateToProps = (state) =>{
    return {
        finanza : state.createFinance
    }
}

const mapDispatchToProps = {
    createFinance
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateFinance);