import { createAction } from 'redux-actions';
import { e_port , e_route } from '../../thirdParty/params'
import axios from 'axios';

export const handleApiError = createAction('handleAppiError');
export const getUserSuccess = createAction('getUserSuccess');
export const removeUserSuccess = createAction('removeUserSuccess');
export const getListUserSuccess = createAction('getUserSuccess');
export const createUserSuccess = createAction('createUserSuccess');

export const getListUsers = (range, page) => {
    return async dispatch => {
        return axios.get(`${e_route}:${e_port}/usuario`)
        .then(( data ) => {
            dispatch(getUserSuccess(data));
       }).catch((error) => {
            dispatch(handleApiError(error));
       });
      }
}

export const getUserById = (id) => {
    return async dispatch => {
        return axios.get(`${e_route}:${e_port}/usuario/${id}`)
        .then((data) => {
            console.log(data)
            dispatch(getUserSuccess(data));  
        }).catch((error) => {
            console.log(error)
            dispatch(handleApiError(error));
        });
    }
}

export const removeUserById = (id) => {
    return async dispatch => {
        return axios.get(`${e_route}:${e_port}/usuario/bloquear/${id}`)
        .then((data) => {
            dispatch(removeUserSuccess(data));  
        }).catch((error) => {
            dispatch(handleApiError(error));
        });
    }  
}

export const editUserById = (cod_usuario, nombre_usuario, apellido_usuario, correo_usuario, cod_rol, cod_estado) => {
    return async dispatch => {
        return axios.post(`${e_route}:${e_port}/usuario`, {
           
            cod_usuario, nombre_usuario, apellido_usuario, correo_usuario, cod_rol, cod_estado
        })
        .then((data) => {
            dispatch(getUserSuccess(data))   
        }).catch((error) => {
            dispatch(handleApiError(error));   
        });
    }
}

export const createUser = (nombre_usuario, apellido_usuario, correo_usuario, cod_rol, cod_estado,clave_usuario) => {
    return async dispatch => {
        return axios.put(`${e_route}:${e_port}/usuario`, {
             nombre_usuario, apellido_usuario, correo_usuario, cod_rol, cod_estado, clave_usuario
        })
        .then((data) => {
            dispatch(createUserSuccess(data))   
        }).catch((error) => {
            alert(error)

            dispatch(handleApiError(error));   
        });
    }
}
