import React from 'react';
import IProduct from './page';

//REACT-REDUX dependencies
import { connect } from 'react-redux';
import { getEntregado, getEnviado } from '../../../../actions/general/producto'

//ANT DEPENDENCIES
import { Modal, Button, Progress } from 'antd';

class Product extends React.Component{
  
  state = {setVisible: false, setVisibleOnSecondModal : false ,  userRemoveId: 0}


   componentDidMount(){
    this.props.getEntregado();
    this.props.getEnviado();
  }

   onChangePage = (p) => {
     console.log(p);
   }

  render(){ 
    const { listEntregado, listEnviado } = this.props;
    
    return(
      <React.Fragment>
        <IProduct listEntregado={listEntregado}  listEnviado={listEnviado} onChangePage={this.onChangePage.bind(this)}/>
      </React.Fragment>
    );

  }

}

const mapStateToProps = (state) => {
  return{
    listEntregado : state.getProductoEntregado,
    listEnviado : state.getProductoEnviado
  }
}

const mapDispatchToProps = {
    getEntregado,
    getEnviado
}

export default connect(mapStateToProps, mapDispatchToProps)(Product);