import React from 'react';
import INomina from './page';
import {connect} from 'react-redux'
import { getListNomina, getNominaByUser} from '../../../../actions/general/nomina'



//ANT DEPENDENCIES
import { Modal, Button, Progress } from 'antd';
import { Table } from 'antd';
const { Column, ColumnGroup } = Table;

class Nomina extends React.Component {

    state = { 
        viewDialogDetail : false,
        idUserDetail : 0
     }

    componentDidMount(){
        this.props.getListNomina(1,20);
    }

    viewDetail(id) {
        this.props.getNominaByUser(id);
        this.setState({
            viewDialogDetail: true,
            idUserDetail: id
        })
    }

    closeDetail = () => {
        this.setState({
            viewDialogDetail: false
        })
    }


    isReady(){
        if (this.props.listNominaByUser.json_data.message === void 0 ) {
            return true;
           }else{
             return false;
        }   
      }

      showTableInformation(){
          return(
                <Table dataSource={this.props.listNominaByUser.json_data.message.prestaciones}  pagination={false} >


                <Column
                    title="Nombre de prestación"
                    dataIndex="nom_prestacion"
                    key="nom_prestacion"
                />
                <Column
                    title="Descripción"
                    dataIndex="descripcion_prestacion"
                    key="descripcion_prestacion"
                />

                <Column
                title="Valor"
                dataIndex="valor"
                key="valor"
                />
       
            </Table>
          );
      }

    render(){
        const { listNomina, listNominaByUser } = this.props;
        return(
            <React.Fragment>
                <INomina viewDetail={this.viewDetail.bind(this)} listNomina={listNomina} />

                <Modal visible={this.state.viewDialogDetail}  onOk={this.closeDetail} onCancel={this.closeDetail}>
                      {this.isReady() ? <p>none</p> : <div><center><h4>{this.props.listNominaByUser.json_data.message.nombre_usuario} {this.props.listNominaByUser.json_data.message.apellido_usuario}</h4></center>{this.showTableInformation()}</div>}
                      
                </Modal>
             </React.Fragment>
        );
    }
}

const mapStatetoProps = (state) => {
    return {
        listNomina : state.nomina,
        listNominaByUser : state.getNominaByUser
    }
}

const mapDispatchToProps = {
        getListNomina,
        getNominaByUser
    
}

export default connect(mapStatetoProps,mapDispatchToProps)(Nomina);