import React from 'react';
import './AuthLayoutStyle.css';

  function AuthLayout(props){

    return(
      <div className="container-fluid">
          <div className="row">
            <div className="col-sm-6 leftColor"></div>
            <div className="col-sm-6 rightColumn">
                <div className="col-9 col-centered">
                      {props.children}
                </div>
            </div>
         </div>

      </div>
    );
  }
  export default AuthLayout;