import { createAction } from 'redux-actions';
import axios from 'axios';

export const getRoleSuccess = createAction('getRoleSuccess');
export const handleApiError = createAction('handleApiError');

export const getListRols = () => {
    return async dispatch => {
        return axios.get('http://localhost:8000/rol/getListRols').then((data) => {
            dispatch(getRoleSuccess(data))
        }).catch((error) => {
            dispatch(handleApiError(error))
        });
    }
}