import { createAction } from 'redux-actions';
import { e_port , e_route } from '../../thirdParty/params'
import axios from 'axios';
import { AUTH_ERROR } from '../authentication/types';

export const handleApiError = createAction('handleApiError');

export  const getFinanzaIngresoSuccess = createAction('getFinanzaIngresoSuccess');
export  const getFinanzaEgresoSuccess = createAction('getFinanzaEgresoSuccess');
export  const createFinanzaSuccess = createAction('createFinanzaSuccess');


export const getIngreso = () => {
    return async dispatch => {
        return axios.get(`${e_route}:${e_port}/finanza/ingreso`)
        .then(( data ) => {
            dispatch(getFinanzaIngresoSuccess(data));
       }).catch((error) => {
           alert(error)
            dispatch(handleApiError(error));
       });
      }
}

export const getEgreso = () => {
    return async dispatch => {
        return axios.get(`${e_route}:${e_port}/finanza/egreso`)
        .then(( data ) => {
            dispatch(getFinanzaEgresoSuccess(data));
       }).catch((error) => {
            dispatch(handleApiError(error));
       });
      }
}


export const createFinance = (cod_tipo_finanza, cod_tipo_transaccion, val_transaccion) => {
    return async dispatch => {
        return axios.put(`${e_route}:${e_port}/finanza`, {
            cod_tipo_finanza, cod_tipo_transaccion, val_transaccion, fecha_transaccion: '2019-04-25'
        })
        .then(( data ) => {
            dispatch(createFinanzaSuccess(data));
       }).catch((error) => {
            dispatch(handleApiError(error));
       });
      }
}




