import { getUserSuccess ,handleApiError,removeUserSuccess } from '../../actions/general/user';
import { handleActions } from 'redux-actions';
import { errors } from '../../dependencies/lang/es'

const defaultState = {
    json_data: [],
    message: '',
    success: '',
    loading: true  
};

export default handleActions({
    [handleApiError]:(state, {type, payload}) => {
        return { ...state, json_data: [], message: errors.no_data_found , success:false, loading:true}
    },
    [removeUserSuccess]:(state, {type, payload}) => {
        return { ...state, json_data: [], message: "La operación se ha realizado correctamente" , success:true, loading:false}
    }

    



}, defaultState)
