import React from 'react';
import {Link} from 'react-router-dom'
//ANT DEPENDENCIES
import { Card } from 'antd';
import { Pagination } from 'antd';
import { Table, Button } from 'antd';
const { Column, ColumnGroup } = Table;

function IListUsers(props){
    const { listUsers, onChangePage, handleSubmit,deleteUser  } = props;
        console.error( listUsers)
    return(
    <div style={{ padding: '30px' }}>
    <Card title="Listado de usuarios" bordered={true} > 
    <Link to="/admin/createUser">Crear usuario</Link>
    <br></br>
        <Table dataSource={listUsers.json_data.message}  pagination={false} >

            <Column
            title="CODIGO"
            dataIndex="cod_usuario"
            key="cod_usuario"
            />

            <ColumnGroup title="Nombre Completo">
            <Column
                title="Primer Nombre"
                dataIndex="nombre_usuario"
                key="nombre_usuario"
            />
            <Column
                title="Segundo Nombre"
                dataIndex="apellido_usuario"
                key="apellido_usuario"
            />
            </ColumnGroup>


            <Column
            title="Correo Electrónico"
            dataIndex="correo_usuario"
            key="correo_usuario"
            />

            <Column
            title="Rol o cargo"
            dataIndex="rol.nombre_rol"
            key="rol.nombre_rol"
            />

            <Column
            title="Acción"
            key="action"
            render={(text, record) => (
           
                <span>
                <label><Link to={'editUser/'+text.cod_usuario} >Editar Usuario</Link></label>
                <label>
                <form className="login-form">
                    <Button onClick={() => deleteUser(text.cod_usuario)} type="warning" >Deshabilitar</Button>
                </form>
                </label>

                </span>
            )}
            />
        </Table>

        <Pagination current={listUsers.json_data.current_page} onChange={onChangePage} total={listUsers.json_data.total} />

    </Card>
    </div>
    );

}

export default IListUsers;