import { combineReducers } from 'redux';
import { reducer as formReducer } from 'redux-form';
import authReducer from './authentication/authentication'
import categories from './general/categories'
import user from './general/user';
import removeUser from './general/removeUser';
import rol from './general/rol'
import nomina from './general/getNomina'
import getNominaByUser from './general/getNominaByUser'
import createUser from './general/createUser'
import getMateriaPrimaProduccion from './general/getMateriaPrimaProduccion'
import getMateriaPrimaAlmacenada from './general/getMateriaPrimaAlmacenada'
import getProductoEntregado from './general/getProductoEntregado'
import getProductoEnviado from './general/getProductoEnviado'
import getFinanzaEgreso from './general/getFinanzaEgreso'
import getFinanzaIngreso from './general/getFinanzaIngreso'
import createFinance from './general/createFinance'

export default combineReducers({
    form: formReducer,
    auth: authReducer,
    categories: categories,
    user,
    removeUser,
    rol,
    nomina,
    getNominaByUser,
    createUser,
    getMateriaPrimaProduccion,
    getMateriaPrimaAlmacenada,
    getProductoEntregado,
    getProductoEnviado,
    getFinanzaEgreso,
    getFinanzaIngreso,
    createFinance


});
