import React from 'react';
import {Link} from 'react-router-dom'
import { Card } from 'antd'
import { Pagination } from 'antd';
import { Table, Button } from 'antd';
const { Column, ColumnGroup } = Table;

function INomina(props){
    const { listNomina, viewDetail } = props;
    return(
        <Card border={true} title="Nómina">
        <Table dataSource={listNomina.json_data.message}  pagination={false} >


            <ColumnGroup title="Empleado">
            <Column
                title="Nombres"
                dataIndex="nombre_usuario"
                key="nombre_usuario"
            />
            <Column
                title="Apellidos"
                dataIndex="apellido_usuario"
                key="apellido_usuario"
            />

            <Column
            title="Rol"
            dataIndex="nombre_rol"
            key="nombre_rol"
            />

            <Column
            title="Departamento"
            dataIndex="nombre_departamento"
            key="nombre_departamento"
            />
            </ColumnGroup>

            <Column
            title="Total de prestaciones"
            dataIndex="prestaciones"
            key="prestaciones"
            />

            <Column
            title="Salario"
            dataIndex="valor_salario"
            key="valor_salario"
            />


            <Column
            title="Salario total"
            dataIndex="total"
            key="total"
            />

            <Column
            title="Acción"
            key="action"
            render={(text, record) => (
           
                <span>
                <label>
                <form className="login-form">
                    <Button onClick={() => { viewDetail(text.cod_usuario) }} type="warning" >Ver detalle</Button>
                </form>
                </label>

                </span>
            )}
            />
        </Table>


        </Card>
    );
}
export default INomina;