import React from 'react';
import ICreateUser from './page';
//REDUX DEPENDENCIES
import {connect} from 'react-redux';
import { compose } from 'redux';
import { createUser} from '../../../../actions/general/user';
import { getListRols } from '../../../../actions/general/rol';
//ANT DEPENDENCIES
import { Modal, Button, Progress } from 'antd';


class CreateUser extends React.Component{
    state = { visible: false, errorsValidation: false }
    constructor(props) {
      super(props);
    
    }
  
    componentDidMount(){
      //TODO
   
    }

    async onSubmit(formData){
      //se validan los datos
      //se actualizan los datos recibidos
      this.props.createUser( formData.nombre_usuario, formData.apellido_usuario, formData.correo_usuario, formData.cod_rol, formData.cod_estado, formData.clave_usuario);
      this.setState({
        visible: true,
    });
    }

    handleOk = (e) => {
      console.log(e);
      this.setState({
          visible: false,
      });
    }

    handleError = (e) => {
      this.setState({
        visible: true,
        errorsValidation: true
      });
    }
    
    handleCancel = (e) => {
      console.log(e);
      this.setState({
        visible: false,
      });
    }

    isReady(){
      if (this.props.user.json_data.message === void 0 ||this.props.user.json_data.message.rol === void 0) {
          return true;
         }else{
           return false;
      }   
    }

    

    render(){
      const { user, rol } = this.props;
      return(
        <React.Fragment>
            <ICreateUser user={user} rol={rol} onSubmit={this.onSubmit.bind(this)} />

            <Modal
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
            >

            {this.props.user.success && <center><Progress type="circle" percent={100} /><h2>¡Perfecto!</h2>La operación se ha realizado correctamente.</center> || 
            <center><Progress type="circle" percent={100} status="exception" /><h2>Ha ocurrido algo..</h2>La operación no se pudo realizar.</center> }
            </Modal>

        </React.Fragment>
      );
    }

}

const mapStateToProps = (state) => {
   return {
      user:state.createUser,
      rol:state.rol,
   }
}

const mapDispatchToProps = {
   createUser
}

export default compose(connect( mapStateToProps, mapDispatchToProps))(CreateUser);