import { createAction } from 'redux-actions';
import { e_port , e_route } from '../../thirdParty/params'
import axios from 'axios';

export const handleApiError = createAction('handleApiError');

export  const getListCategoriesSuccess = createAction('getListCategoriesSuccess');

export const getListCategories = (id) => {
    return async dispatch => {
        return axios.get(`${e_route}:${e_port}/usuario/permisos/${id}`)
        .then(( data ) => {
            dispatch(getListCategoriesSuccess(data));
       }).catch((error) => {
           alert(error)
            dispatch(handleApiError(error));
       });
      }
}




