import { AUTH_SUCCESS, AUTH_ERROR } from '../../actions/authentication/types'
const DEFAULT_STATE = {
    isAuthenticated : false,
    authToken: '',
    errorMessage: ''
}

export default(state = DEFAULT_STATE, action) => {
    switch(action.type){
        case AUTH_SUCCESS:
            return { ...state, authToken: action.payload, isAuthenticated: true, errorMessage: ''  }

        case AUTH_ERROR:
            return { ...state, isAuthenticated: false, errorMessage: action.payload }
            
        default:
        return state;
    }
}