import React from 'react';
import ISider from './page';
//react-redux components or dependencies
import { withRouter } from 'react-router-dom'

class SiderComponent extends React.Component{
    constructor(){
        super();
        this.getCurrentPageId = this.getCurrentPageId.bind(this);
    }

     getCurrentPageId(){
       return this.props.categories.json_data.filter(x => this.props.location.pathname === x.route_permission)[0]
      }

      isReady(){
          console.warn(this.props.categories.json_data)
        if (this.props.categories.json_data=== void 0) {
            return true;
           }else{
             return false;
        }   
      }


    render(){
        return(
            <ISider {...this.props} currentPageId={this.getCurrentPageId} /> 
          
        );
    }
}


export default withRouter(SiderComponent);