import React from 'react';
import IEntry from './page';

//REACT-REDUX dependencies
import { connect } from 'react-redux';
import { getIngreso } from '../../../../actions/general/finanza'

//ANT DEPENDENCIES
import { Modal, Button, Progress } from 'antd';

class Entry extends React.Component{
  
  state = {setVisible: false, setVisibleOnSecondModal : false ,  userRemoveId: 0}


   componentDidMount(){
    this.props.getIngreso();
  }

   onChangePage = (p) => {
     console.log(p);
   }

  render(){ 
    const { listIngreso } = this.props;
    
    return(
      <React.Fragment>
        <IEntry listIngreso={listIngreso}   onChangePage={this.onChangePage.bind(this)}/>
      </React.Fragment>
    );

  }

}

const mapStateToProps = (state) => {
  return{
    listIngreso : state.getFinanzaIngreso
  }
}

const mapDispatchToProps = {
    getIngreso
}

export default connect(mapStateToProps, mapDispatchToProps)(Entry);