import axios from "axios";
import { AUTH_SUCCESS, AUTH_ERROR } from './types'
import { e_port , e_route } from '../../thirdParty/params'

/**
 *  ActionCreators -> Create/return Actions -> Dispatched -> middlewares -> reducers
 */

 export const login = data => {
     return async dispatch =>  {
        try{
            JSON.stringify(data)
            const res =  await axios.post(`${e_route}:${e_port}/auth/login`, data)   
            var myToken = res.data['token'];
            var myData = res.data;

            console.log(myData)

            dispatch({
                type: AUTH_SUCCESS,
                payload: myData
            })
            //save it on 'SessionStorage'.
            sessionStorage.setItem('myToken', myToken)
        }catch(e){
            alert("no hay conexion")
            dispatch({
                type: AUTH_ERROR,
                payload: 'Usuario y/o contraseña incorrectos.'
            })

        }
     }
 }
