import React from 'react';
import IRawMaterial from './page';

//REACT-REDUX dependencies
import { connect } from 'react-redux';
import { getProduccion, getAlmacenada } from '../../../../actions/general/materiaprima'

//ANT DEPENDENCIES
import { Modal, Button, Progress } from 'antd';

class RawMaterial extends React.Component{
  
  state = {setVisible: false, setVisibleOnSecondModal : false ,  userRemoveId: 0}


   componentDidMount(){
    this.props.getProduccion();
    this.props.getAlmacenada();
  }

   onChangePage = (p) => {
     console.log(p);
   }

  render(){ 
    const { listProduccion, listAlmacenada } = this.props;
    
    return(
      <React.Fragment>
        <IRawMaterial listProduccion={listProduccion}  listAlmacenada={listAlmacenada} onChangePage={this.onChangePage.bind(this)}/>
      </React.Fragment>
    );

  }

}

const mapStateToProps = (state) => {
  return{
    listProduccion : state.getMateriaPrimaProduccion,
    listAlmacenada : state.getMateriaPrimaAlmacenada
  }
}

const mapDispatchToProps = {
    getProduccion,
    getAlmacenada
}

export default connect(mapStateToProps, mapDispatchToProps)(RawMaterial);