import React from 'react';
import ICreateNomina from './page';
import { e_port , e_route } from '../../../../thirdParty/params'
class CreateNomina extends React.Component{
    state = ({ prestacion : '', listOption:[], isReady:false })

    constructor(props){
        super(props)
        
        this.onSubmit = this.onSubmit.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.onChange = this.onChange.bind(this);
        fetch(e_route +":"+e_port+"/usuario/listNomina/"+this.props.match.params.id,{ method:'GET' }).then(data => data.json()).then(data=>this.setState({listOption:JSON.stringify(data.message)})).then(e=>this.setState({isReady:true})).catch(error => alert(error))

    }
    


 

    onSubmit(e){
        e.preventDefault();
        fetch(e_route + ":"+e_port+"/usuario/add/prestacion?"+this.props.match.params.id+"&cod_prestacion="+this.state.prestacion,{method:'PUT'}).then(data => alert("enviado")).catch(error =>alert("ok"))
    }
    handleSubmit = (e) =>{}
    onChange(e){
       
        console.log(e.target.value)
        this.setState({

            prestacion: e.target.value
        })
    }
  
    render(){
        return(
            <React.Fragment>
                {this.state.isReady ?<ICreateNomina handleSubmit={this.handleSubmit} onSubmit={this.onSubmit} onChange={this.onChange}  state={this.state.prestacion} listOption={this.state.listOption}></ICreateNomina>:'Espera...'}
            </React.Fragment>
        );
    }

} export default CreateNomina;