import React from 'react';
import { Link } from 'react-router-dom'; 

//ANT DESING DEPENDENCIES
import { Layout, Menu, Icon } from 'antd';
const { Sider } = Layout;
const SubMenu = Menu.SubMenu;
//END ANT DESING DEPENDENCIES

function ISider(props){
const { categories } = props;
//const { isSiderCollapsed } = false;
console.log(props.location.pathname)

//console.log( categories.json_data.filter(function(x){ return x.route_permission == props.location.pathname }).map(cod => {return (cod.cod_permission.toString())}))
  return(
    
  <Sider
    breakpoint="lg"
    collapsedWidth="0"
    onBreakpoint={(broken) => { console.log(broken); }}
    onCollapse={(collapsed, type) => { console.log(collapsed, type); }}
  >
    <div className="logo" />
    <Menu theme="dark" mode="inline" defaultSelectedKeys={["1"]}>


      {categories.json_data.filter(function (x){
        return x.child_permission >= -1 || x.child_permission <= 0;
      }).map(x => {
        if(x.child_permission == "-1")  // (-1) significa no sub-categoría
        return (      
        <Menu.Item key={x.cod_permission}>
        <Link to={x.route_permission} >
         <Icon type="pie-chart" />
         <span>{x.name_permission}</span>
         </Link>
        </Menu.Item>
        );
  
        if(x.child_permission == "0")  // (0) significa que tiene sub-categorias
        return (      
        <SubMenu 
        key={x.cod_permission}
        title={x.name_permission}
        >

        {categories.json_data.filter(function(id){
          return id.child_permission == x.cod_permission
        }).map(subItem => {
          return(
           
            <Menu.Item key={subItem.cod_permission}><a href={subItem.route_permission}>{subItem.name_permission}</a></Menu.Item>
       
          );
        })}
        
        </SubMenu>
        );
      })}



    </Menu>
  </Sider>
  );

}


export default ISider;