import { getRoleSuccess ,handleApiError } from '../../actions/general/rol';
import { handleActions } from 'redux-actions';
import { errors } from '../../dependencies/lang/es'

const defaultState = {
    json_data: [],
    message: '',
    success: '',
    loading: true  
};

export default handleActions({
    [getRoleSuccess]:(state, {type, payload}) => {  
        return { ...state, json_data: payload.data, message: undefined, success:true, loading:false}
    },
    [handleApiError]:(state, {type, payload}) => {
        return { ...state, json_data: [], message: errors.no_data_found , success:true, loading:false}
    }
}, defaultState)
