import React from 'react';
import { TextField , SelectField} from 'redux-form-antd';
import { Form } from 'antd';
function ICreateNomina(props){
    const { onSubmit, handleSubmit,onChange,prestacion,listOption  } = props;
    return(
        <React.Fragment>
             <form onSubmit={onSubmit} className="login-form">
                    <div class="row">

                            <label for="tipo_prestacion" >Tipo de prestación</label>
                            <select class="form-control" name="tipo_prestacion" value={prestacion || ''} onChange={onChange}>
                            <option name="">Selecciona una opción..</option>
                            {JSON.parse(listOption).map(x=>{
                                return(
                                    <option name={x.cod_prestacion}>{x.nom_prestacion}</option>
                                )
                            })}
                             
                            </select>
                            <br /><br />
                            <button class="btn btn-outline-primary" onClick={() => onSubmit}> Enviar</button>
                    </div>
             </form>
        </React.Fragment>
    );

} export default ICreateNomina;